import { Component, OnInit } from '@angular/core';
import { ModalForm } from 'src/app/models/modal-form';
import { ModalFormInterface } from 'src/app/interfaces/modal-form-interface';
import { ModalController } from '@ionic/angular';
import { Model } from 'src/app/api/models/model';
import { RequestService } from 'src/app/api/request.service';
import { ActivatedRoute, UrlSegment } from '@angular/router';
import { DialogService } from 'src/app/api/util/dialog.service';
import { UtilService } from 'src/app/services/util.service';


@Component({
  selector: 'app-payment',
  templateUrl: './payment.page.html',
  styleUrls: ['./payment.page.scss'],
})
export class PaymentPage implements OnInit  {
  
  public modelPayment : Model;
  public payment_id : any;
  public payment : any;



  constructor(
    public request : RequestService,
    public route : ActivatedRoute,
    public dialogService : DialogService,
    public util : UtilService
  ) {
    this.modelPayment = new Model("Payment",request);

    this.payment_id = this.route.snapshot.paramMap.get('payment_id');


   }

  ngOnInit() {
    this.initObject();
  }


  async initObject() {

    await this.dialogService.loadingProgres();
    
    if(this.payment_id){
      this.modelPayment.api_find(this.payment_id).subscribe(
        response => {
            this.dialogService.dimissLoaging();
            if(response['status'] == 'success'){
              this.payment = response['Payment'];
            }
        },
        error => {
          this.dialogService.dimissLoaging();
        }
      )
    }
  }

 
get isApproved(){
  return this.payment ? this.payment.status == 'approved' : false;
}

get noPaid(){
  return this.payment ? ( this.payment.status == 'created' || this.payment.status == 'pending' ) : false ;
}

get isFailure(){
  return this.payment ? this.payment.status == 'rejected'  : false;
}

get status(){

  if(! this.payment)
    return '';

  switch (this.payment.status) {
    case 'created' : 
      return 'Pendiente';

    case 'pending': 
      return 'Pendiente  de pago';
    
    case 'approved' : 
      return 'Pagado';
    
    case 'rejected' :
      return 'Pago rechazado';
  }

}

}
